
    .container {
        margin: auto;
        padding-left: 15px;
        padding-right: 15px;
    
        @media screen and (min-width: 450px) {
          width: 480px;
        }
    
        @media screen and (min-width: 768px) {
          width: 768px;
        }
    
        @media screen and (min-width: 1200px) {
          width: 1200px;
        }
      }
      
    